<template>
  <div class='hundred'>
    <div style='height:1px'></div>
    <div class='aisleBack' v-for='item in aisleList' :key='item.acqcode'>
        <div>
            <div class='allFlex aisleJust'>
                <div>
                    <img src="../../assets/bank/yl.png" class='bankLogo bankLeft' />
                    <span class='bankRight weightFont'>{{item.channelName}}</span>
                </div>
                <span class='gary'>{{item.remark}}</span>
            </div>
        </div>
        <div class='aisleContent allFlex aisleJust border'>
            <div class='textCenter'>
                <p class='aisleBlue'>{{item.rate}}%</p>
                <p class='gary'>使用费率</p>
            </div>
            <div>
                <p class='gary'>单笔限额：{{item.limit}}</p>
                <p class='gary'>交易时间：{{item.T0date}}</p>
            </div>
            <div>
                <a class='aisleNext' @click="aisle === 'YK' ? toYk(item.acqcode, item.rate, aisle): aisle === 'QYK' ? toYk(item.acqcode, item.rate, aisle) : toNext(item.acqcode, item.rate, aisle)">选择</a>
            </div>
        </div>
        <div class='allFlex gary aisleJust redColor'>
            <div class='aisleDescription'>通道说明</div>
            <div class='aisleRemarks'>{{item.remarks}}</div>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      version: '',
      agentNo: '',
      merchantNo: '',
      aisleList: [],
      bank: {},
      money: 0,
      mnum: 0,
      aisle: '',
      cardMore: [],
      usermerchantNo: '',
      usermerchantNotwo: ''
    }
  },
  created () {
    this.version = this.$stact.state.version
    this.agentNo = this.$stact.state.agentNo
    this.merchantNo = JSON.parse(this.$stact.state.token)[0].merchantNo
    this.bank = JSON.parse(this.$route.query.item)
    this.money = this.$route.query.money
    this.aisle = this.$route.query.aisle
    this.usermerchantNo = this.$route.query.merchantNo
    this.usermerchantNotwo = this.$route.query.merchantNotwo
    this.list()
    this.cardList()
  },
  methods: {
    list () {
      let vm = this
      if (vm.usermerchantNo) {
        vm.merchantNo = vm.usermerchantNo
      }
      if (this.usermerchantNotwo) {
        vm.merchantNo = vm.usermerchantNotwo
      }
      let parmas = {
        '0': '0700',
        '3': '390013',
        '42': vm.merchantNo,
        '43': vm.aisle,
        '59': vm.version
      }
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      vm.$http.get(`request.app${url}`)
        .then(res => {
          if (res.data[39] === '00') {
            vm.aisleList = JSON.parse(res.data[57]).acqData
            console.log(vm.aisleList)
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    cardList () {
      let vm = this
      if (vm.usermerchantNo) {
        vm.merchantNo = vm.usermerchantNo
      }
      if (this.usermerchantNotwo) {
        vm.merchantNo = vm.usermerchantNotwo
      }
      let parmas = {
        '0': '0700',
        '3': '390021',
        '42': vm.merchantNo,
        '45': vm.bank.BANK_ACCOUNT,
        '59': vm.version
      }
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      vm.$http.get(`request.app${url}`)
        .then(res => {
          if (res.data[39] === '00') {
            vm.cardMore = JSON.parse(res.data[36])
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    toYk (acqcode, rate, aisle) {
      let vm = this
      vm.cardMore.map((x, i) => {
        if (x.code.toString() === acqcode.toString()) {
          if (x.status === '开通') {
            vm.$router.push({ name: 'make', query: { acqcode: acqcode, rate: rate, item: vm.$route.query.item, tong: aisle, merchantNo: vm.usermerchantNo } })
          } else {
            vm.$router.push({ name: 'tiedcard', query: { item: JSON.stringify(vm.bank), code: x.code, category: x.category, merchantNo: vm.usermerchantNo } })
          }
        }
      })
    },
    toNext (acqcode, rate) {
      let vm = this
      vm.cardMore.map((x, i) => {
        if (x.code.toString() === acqcode.toString()) {
          if (x.status === '开通') {
            vm.mnum = 1
            vm.toMoney(acqcode, rate)
          } else {
            vm.mnum = 1
            vm.$router.push({ name: 'tiedcard', query: { item: JSON.stringify(vm.bank), code: x.code, category: x.category, tong: aisle, merchantNo: vm.usermerchantNo } })
          }
        } else {
          if (i === vm.cardMore.length - 1 && vm.mnum === 0) {
            vm.mnum = 0
            vm.toMoney(acqcode, rate)
          }
        }
      })
    },
    toMoney (acqcode, rate) {
      let vm = this
      if (vm.usermerchantNo) {
        vm.merchantNo = vm.usermerchantNo
      }
      let parmas = {
        '0': '0700',
        '3': '190959',
        '5': vm.money * 100,
        '41': vm.bank.BANK_ACCOUNT,
        '42': vm.merchantNo,
        '43': acqcode,
        '44': rate,
        '45': vm.bank.CVN,
        '46': vm.bank.EXPDATE,
        '59': vm.version
      }
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      vm.$http.get(`request.app${url}`)
        .then(res => {
          if (res.data[39] === '00') {
            vm.$router.push({ name: 'ifarme', query: { url: res.data[30], title: '通道绑卡', merchantNo: vm.usermerchantNo } })
          } else {
            vm.$message({
              message: res.data[39],
              center: true,
              offset: 30,
              duration: 2500,
              type: 'success'
            })
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>
